@use "sass:math";

// インナー幅設定
// コンテンツのインナー幅（最も共通の幅が多い部分）
$inner: 1100px;
// ※キュンパス幅ではなくインナーの幅を指す

// 余白
$padding-pc: 25px;
$padding-sp: 20px;

////////////////////
// フォント設定
////////////////////

$main-font: 'Noto Sans JP', sans-serif;
$second-font: 'Noto Serif JP', serif;

// フォントウェイト
$regular: 400;
$bold: 700;

////////////////////
// 色の指定
////////////////////

//色指定（共通性のあるものは出来る限り指定すると良い）
$black: #111;
$white: #fff;
$gray: #333333;
$yellow: #FFE82F;

////////////////////
// bodyの指定
////////////////////

body {
  background: $black;
  font-family: $main-font
}
