@charset "UTF-8";
body {
  background: #111;
  font-family: "Noto Sans JP", sans-serif;
}

.u-desktop {
  display: none;
}
@media screen and (min-width: 768px) {
  .u-desktop {
    display: block;
  }
}

@media screen and (min-width: 768px) {
  .u-mobile {
    display: none;
  }
}

/* リキッドレイアウト対応 */
html {
  font-size: 16px;
}
@media (max-width: 375px) {
  html {
    font-size: 4.2666666667vw;
  }
}
@media screen and (min-width: 768px) {
  html {
    font-size: 1.4545454545vw;
  }
}
@media (min-width: 1100px) {
  html {
    font-size: 16px;
  }
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default padding */
ul,
ol {
  padding: 0;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
ul,
ol,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Set core root defaults */
html {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/* Remove list styles on ul, ol elements with a class attribute */
ul,
ol {
  list-style: none;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img {
  max-width: 100%;
  display: block;
  width: 100%;
}

/* Natural flow and rhythm in articles by default */
article > * + * {
  margin-top: 1em;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Blur images when they have no alt attribute */
img:not([alt]) {
  filter: blur(10px);
}

/* フォームリセット */
input,
button,
select,
textarea {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background: transparent;
  border: none;
  border-radius: 0;
  font: inherit;
  outline: none;
}

textarea {
  resize: vertical;
}

input[type=checkbox],
input[type=radio] {
  display: none;
}

input[type=submit],
input[type=button],
label,
button,
select {
  cursor: pointer;
}

select::-ms-expand {
  display: none;
}

/* ホバー */
a {
  text-decoration: none;
  color: inherit;
  transition: opacity 0.3s;
}

.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 3.125rem;
  background: transparent;
  z-index: 1000;
}

.header__inner {
  height: inherit;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 1.375rem;
}
@media screen and (min-width: 768px) {
  .header__inner {
    padding-left: 2rem;
    padding-right: 0;
  }
}

.header__logo {
  color: #fff;
  font-size: 1.25rem;
  letter-spacing: 0.075rem;
}
@media screen and (min-width: 768px) {
  .header__logo {
    font-size: 1.875rem;
  }
}

.hamburger {
  position: relative;
  width: 1.5rem;
  height: 0.875rem;
}
@media screen and (min-width: 768px) {
  .hamburger {
    display: none;
  }
}

.hamburger span {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 0.125rem;
  background-color: #fff;
}

.hamburger span:nth-child(1) {
  top: 0;
}

.hamburger span:nth-child(2) {
  top: 0.4375rem;
}

.hamburger span:nth-child(3) {
  top: 0.875rem;
}

.hamburger.is-active span:nth-child(1) {
  top: 0.4375rem;
  transition: 0.1s transform 0.1s, background-color;
  transform: rotate(-45deg);
  background-color: #fff;
}

.hamburger.is-active span:nth-child(2) {
  transition: 0.1s opacity;
  opacity: 0;
}

.hamburger.is-active span:nth-child(3) {
  top: 0.4375rem;
  transition: 0.1s transform 0.1s, background-color;
  transform: rotate(45deg);
  background-color: #fff;
}

.header-nav {
  display: none;
  height: inherit;
}
@media screen and (min-width: 768px) {
  .header-nav {
    display: block;
  }
}

.header-nav__inner {
  height: inherit;
}

.header-nav__items {
  height: inherit;
  display: flex;
}

.header-nav__item {
  height: inherit;
  padding: 0 1rem;
}

.header-nav__item a {
  height: inherit;
  display: grid;
  place-items: center;
  font-size: 1rem;
  letter-spacing: 0.125rem;
  line-height: 2.1875;
  font-family: "Noto Sans JP", sans-serif;
  font-weight: 400;
  color: #fff;
}

.header-nav__item.header-nav__item--white {
  padding: 0;
  margin-left: 1rem;
}

.header-nav__item.header-nav__item--white a {
  background-color: #fff;
  padding: 0 2rem;
  color: #111;
}

.drawer {
  display: none;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 5000;
  width: 100%;
  height: 100vh;
}
@media screen and (min-width: 768px) {
  .drawer {
    display: none;
  }
}

.drawer__inner {
  display: flex;
  width: 100%;
  height: inherit;
  align-items: center;
  justify-content: center;
}

.drawer__items {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.drawer__item {
  display: inline-block;
}

.drawer__item:not(:first-child) {
  margin-top: 25px;
}

.drawer__link {
  width: 200px;
  display: inline-block;
  text-align: center;
  font-size: 1rem;
  color: #fff;
  padding-bottom: 25px;
  letter-spacing: 0.05em;
}

.btn {
  max-width: 22.5rem;
  width: 100%;
  display: inline-block;
  background-color: #333333;
  padding: 0.5625rem 0;
  border: solid 0.0625rem #fff;
  text-align: center;
}
@media screen and (min-width: 768px) {
  .btn {
    padding: 0.875rem;
  }
}

.btn p {
  font-size: 1.25rem;
  color: #fff;
  letter-spacing: 0.125rem;
  font-family: "Noto Sans JP", sans-serif;
  font-weight: 400;
}

.card {
  max-width: 20.9375rem;
  width: 100%;
}
@media screen and (min-width: 768px) {
  .card {
    max-width: 21.875rem;
  }
}

.card__img img {
  max-width: 20.9375rem;
  width: 100%;
}
@media screen and (min-width: 768px) {
  .card__img img {
    max-width: 21.875rem;
  }
}

.card__body {
  background-color: #fff;
  padding: 0.25rem 1.375rem 1rem;
}
@media screen and (min-width: 768px) {
  .card__body {
    padding: 0.25rem 1.5rem 1rem;
  }
}

.card__title h3 {
  font-size: 1.125rem;
  color: #111;
  letter-spacing: 0.0625rem;
  line-height: 1.9444444444;
  font-family: "Noto Sans JP", sans-serif;
  font-weight: 400;
}

.card__text {
  margin-top: 0.25rem;
}
@media screen and (min-width: 768px) {
  .card__text {
    margin-top: 0.375rem;
  }
}

.card__text p {
  font-size: 0.875rem;
  color: #111;
  letter-spacing: 0.0625rem;
  line-height: 1.8571428571;
  font-family: "Noto Sans JP", sans-serif;
  font-weight: 400;
}

.card__footer {
  display: flex;
  align-items: center;
  margin-top: 0.5rem;
}

.card__tag {
  padding: 0.0625rem 0.3125rem;
  background-color: #FFE82F;
}

.card__tag p {
  font-size: 0.875rem;
  color: #111;
  letter-spacing: 0.0625rem;
  line-height: 1.625;
  font-family: "Noto Sans JP", sans-serif;
  font-weight: 400;
}

.card__date {
  margin-left: auto;
  font-size: 0.875rem;
  color: #111;
  letter-spacing: 0.0625rem;
  line-height: 1.625;
  font-family: "Noto Sans JP", sans-serif;
  font-weight: 400;
}

.inner {
  width: 100%;
  padding-right: 20px;
  padding-left: 20px;
  margin-right: auto;
  margin-left: auto;
}
@media screen and (min-width: 768px) {
  .inner {
    max-width: 1150px;
    padding-right: 25px;
    padding-left: 25px;
  }
}

@media screen and (min-width: 768px) {
  .news__inner {
    display: flex;
    align-items: center;
  }
}

.news__header {
  display: flex;
  align-items: center;
}
.news__date {
  font-size: 1rem;
  color: #fff;
  font-family: "Noto Sans JP", sans-serif;
  font-weight: 400;
}

.news__tag {
  max-width: 6.25rem;
  width: 100%;
  margin-left: 2rem;
  text-align: center;
  padding: 0.25rem 1.3125rem;
  border: 0.0625rem solid #fff;
}
@media screen and (min-width: 768px) {
  .news__tag {
    margin-left: 3rem;
  }
}

.news__tag p {
  font-size: 0.875rem;
  color: #fff;
  font-family: "Noto Sans JP", sans-serif;
  font-weight: 400;
}

.news__body {
  margin-top: 1rem;
}
@media screen and (min-width: 768px) {
  .news__body {
    margin-top: 0;
    margin-left: 3.75rem;
  }
}

.news__text {
  font-size: 1rem;
  color: #fff;
  font-family: "Noto Sans JP", sans-serif;
  font-weight: 400;
}

.news__btn.btn {
  margin-top: 1.75rem;
}
@media screen and (min-width: 768px) {
  .news__btn.btn {
    margin-top: 0;
    margin-left: 3rem;
    max-width: 8.375rem;
    width: 100%;
    padding: 0.125rem 0.1875rem;
    border: none;
    background-color: #fff;
  }
}

.news__btn.btn p {
  color: #111;
  line-height: 1;
}

.section-title {
  font-size: 1.25rem;
  color: #fff;
  letter-spacing: 0.125rem;
  font-family: "Noto Sans JP", sans-serif;
  font-weight: 400;
}
@media screen and (min-width: 768px) {
  .section-title {
    font-size: 1.875rem;
    letter-spacing: 0.03125rem;
  }
}

.sub-section-title {
  font-size: 3.125rem;
  color: #FFE82F;
  letter-spacing: 0.125rem;
  font-family: "Noto Serif JP", serif;
  font-weight: 400;
  opacity: 0.2;
}
@media screen and (min-width: 768px) {
  .sub-section-title {
    font-size: 6.875rem;
  }
}